	module.exports = {"data":{"markdownRemark":{"html":"<p>凌晨看《危险的关系》，描写一种贵族内部的生活以及勾心斗角的人际关系。没有多大的意思，如果</p>\n<p>了解当时一种生活圈子和生活的文化，大概能知道一些。但是从更广泛的角度说，又没有了解什么，只是几个人之间的男女关系。影片有不少暴露镜头，当然已经作了处理。这是西方文化的一个不可缺少的方面，没有这些，就不成戏。最近一些片子，这样的色彩越来越浓厚，不知最后要走到什么样的程度。我想总是有一个极限的，不然的话，就只有XXX电影了。</p>\n<p>厦门来的一个女孩子，叫我\"王爷爷\"，一副纯真的样子。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0017.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0017.md","frontmatter":{"title":"1月25日 星期二"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0017.md absPath of file >>> MarkdownRemark"}}

